main {
  background-image: url("../public/images/back-paper-1.jpg");
}

.banner-left {
  height: 520px;
  background-image: url("../public/images/Kulibin_I_P.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position-y: -80px;
}

.banner-right {
  height: 520px;
  background-image: url("../public/images/back1.jpg");
  background-size: auto;
  background-repeat: repeat;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.bio {
  margin: 0 auto;
  column-count: 2;
  column-rule: 1px solid #ccc;
  column-gap: 2em;
}

@media (max-width: 900px) {
  .bio {
    column-count: 1;
  }
}

.resume {
  padding-right: 30px;
  padding-left: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.resume-image {
  border-radius: 50%;
  width: 50%;
}
